.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
    margin-right: 1rem;
}
.header-bg{
    background-color: rgba(0, 0 0, 0.85);
    transition: 0.3s;
}
.nav-menu{
    display: flex;
    
} 
.nav-menu li{
    padding: 0 1rem;
} 
.nav-menu li a {
    font-weight: 500;
    font-size: 1.2rem;
   
}
.humberger{
    display: none;
}

@media screen and  (max-width:1040px){
    .nav-menu{
      
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 100vh;
      position: absolute;
      top: 0;
      left: -100%; 
      z-index: -3;
      background: rgba(0, 0, 0, 0.9);
      
    } 
    .nav-menu.active{
        left :0;
    }
    .nav-menu li{
        padding: 1rem 0;
    } 
    .nav-menu li a{
        font-size: 2rem;
    }
    .humberger{
        display: initial;
    }
    
}