*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Outfit" , sans-serif;
}
body{
  background-color: #000;
}

h1, h4, p, a{
  text-decoration: none;
  color: #fff;
}
ul{
  list-style-type: none;
}

.btn{
  padding: 12px 32px;
  font-size: 1rem;
  text-transform: uppercase;
  background:  #e834eb ;
  color: #222;
  border: 1px solid #fff;
  font-weight: 600;
  cursor: pointer;
  border-style: dashed;
  border-radius: 2rem;
}
.btn-light{
   background: transparent;
   color: #fff;
}
.btn:hover{
  background: rgba(255,255,255,0.2);
  color: #fff;
  transition: 0.3s;
} 